@import '../mixin.less';
*{
    margin: 0;
    padding: 0;
    list-style: none;
    box-sizing: border-box;
}
html{
    min-width: @minWidth;
    height: 100%;
    background-color: @bgGrey;
    body{
        height: 100%;
        line-height: 1.5;
        color: @black;
        background-color: @white;
        margin: auto;
        overflow: hidden;
        :global(#root){
            height: 100%;
            overflow-x: hidden;
            overflow-y: auto;
        }
    }
}